import React, {Component} from 'react';
import AppLight from './components/AppLight';
import AppDark from './components/AppDark';
import darkstyles from './AppDark.css.js';
import lightstyles from './AppLight.css.js';
import {Link} from 'react-router-dom';
import {Drawer, Navigation, Header, Layout, Button, Content, Footer} from 'react-mdl';
import { library } from '@fortawesome/fontawesome-svg-core';
import { fab } from '@fortawesome/free-brands-svg-icons';
import { fas } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Main from './components/Main';
library.add(fab, fas);

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {mode: 'white', styleUrl: 'App.css', styles: lightstyles};
    this.switchTheme = this.switchTheme.bind(this);
    if(window.matchMedia('(prefers-color-scheme: dark)').matches) {
      this.switchTheme();
    }
  }
  switchTheme = () => {
    if(this.state.mode === "white") {
      this.setState({
        mode: 'black',
        styles: darkstyles,

      });
    } else {
      this.setState({
        mode: 'white',
        styles: lightstyles,
      });
    }
  }

  getContent() {
    if(this.state.mode === 'white') {
      return (
        <AppLight onClick={this.switchTheme} />
      );
    } else {
      return (
        <div>
          <AppDark onClick={this.switchTheme} />
        </div>
      );
    }
  }

  unifiedRender() {
    const footerStyle = {
      width: '100%',
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      margin: 'auto',
    };   
    const fstyle={...footerStyle, ...this.state.styles.headerColor};
    return (
        <div style={{height: '100%', overflowY: 'scroll', overflowX: 'hidden'}}>
                
            <Layout >
              
                <Header style={this.state.styles.headerColor} title="Portfolio" scroll>
                    <Navigation>
                        <Link to="/">Home</Link>
                        <Link to="/resume">Resume</Link>
                        <Link to="/contact">Contact</Link>
                        <Link to="/projects">Projects</Link>
                        <Link to="/about">About</Link>
                    </Navigation>
                </Header>
                <Drawer title="Menu">
                    <Navigation>
                        <Link to="/">Home</Link>
                        <Link to="/resume">Resume</Link>
                        <Link to="/contact">Contact</Link>
                        <Link to="/projects">Projects</Link>
                        <Link to="/about">About</Link>
                    </Navigation>
                </Drawer>
                <Content>
                    <Main styling={this.state.styles}/>
                    
                </Content>
                <Footer style={{...fstyle}}>
                    <div>
                        <Button onClick={this.switchTheme}>
                                <FontAwesomeIcon icon={['fas', 'code']} color={this.state.mode == 'white' ? 'black' : 'white'}></FontAwesomeIcon>
                        </Button>
                    </div>
                </Footer>
            </Layout>

        </div>
    );
  }

  render = () => { 
    return this.unifiedRender();
  }
}

export default App;
