import React from 'react';
import {Grid, Cell, Card, CardTitle, CardActions, IconButton, CardMenu, CardText, Button} from 'react-mdl';

const PythonProjs = (props) => {
    return (
        <div className="projects-display" style={props.styling.headerColor}>
            <Grid className={props.styling.projectsGrid}>
                <Cell col={3}>
                    <Card shadow={3} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{textAlign: "center", color: "#000", height: '150px', background: 'url(https://i.pinimg.com/originals/0f/60/19/0f6019e15f1d8ae07e7e8ea16d242676.png) center / cover'}}></CardTitle>
                        <CardText>Nand2Tetris Hardware Simulator Test Case Generator</CardText>
                        <CardActions border style={{textAlign: "center"}}>
                            <Button colored href="https://github.com/AdilRas/Nand2TetrisCaseGenerator" target="_blank">GitHub</Button>
                            <Button colored href="https://www.nand2tetris.org/course" target="_blank">Nand2Tetris</Button>
                        </CardActions>
                        <CardMenu>
                            <IconButton name="share"/>
                        </CardMenu>
                    </Card>
                </Cell>
            </Grid>
        </div>
    );
}

export default PythonProjs;