import React, {Component} from 'react';
import {Drawer, Navigation, Header, Layout, Button, Content, Footer} from 'react-mdl';
import Main from './Main';
import styles from '../AppDark.css.js';
import {Link} from 'react-router-dom';
import { library } from '@fortawesome/fontawesome-svg-core';
import { fab } from '@fortawesome/free-brands-svg-icons';
import { fas } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
library.add(fab, fas);
const footerStyle = {
    width: '100%',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    margin: 'auto',
};
class AppDark extends Component {
    render() {
        const fstyle={...footerStyle, ...styles.headerColor};

    return (
        <div className="demo-big-content">
            <Layout>
                <Header style={styles.headerColor} title="Portfolio" scroll>
                    <Navigation>
                        <Link to="/">Home</Link>
                        <Link to="/resume">Resume</Link>
                        <Link to="/contact">Contact</Link>
                        <Link to="/projects">Projects</Link>
                        <Link to="/about">About</Link>
                    </Navigation>
                </Header>
                <Drawer title="Menu">
                    <Navigation>
                        <Link to="/">Home</Link>
                        <Link to="/resume">Resume</Link>
                        <Link to="/contact">Contact</Link>
                        <Link to="/projects">Projects</Link>
                        <Link to="/about">About</Link>
                    </Navigation>
                </Drawer>
                <Content>
                    <Main styling={styles}/>
                </Content>
                <Footer style={fstyle}>
                        <div>
                            <Button onClick={this.props.onClick}>
                                    <FontAwesomeIcon icon={['fas', 'code']} color="white"></FontAwesomeIcon>
                            </Button>
                        </div>
                    </Footer>
            </Layout>
        </div>
    );
    }
}

export default AppDark;