import React, {Component} from 'react';
import { Tabs, Tab } from 'react-mdl';
import PythonProjs from './PythonProjs';
import JavaProjs from './JavaProjs';

class Projects extends Component {
    constructor(props) {
        super(props);
        this.state = {
            activeTab: 0,
        };
    }
    getContent() {
        if(this.state.activeTab === 0) {
            return (
                <PythonProjs styling={this.props.styling}/>
            );
        } else if(this.state.activeTab === 1) {
            return (
                <JavaProjs styling={this.props.styling}/>
            );
        } else if(this.state.activeTab === 2) {
            return (
                <h1>Coming soon</h1>
            );
        }
    }
    
    render() {
        return (
            <div className="category-tabs" style={{...this.props.styling.headerColor, height: '100%'}}>
                <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({activeTab: tabId})} ripple>
                    <Tab style={{color: 'white'}}>Python</Tab>
                    <Tab style={{color: 'white'}}>Java</Tab>
                    <Tab style={{color: 'white'}}>C++</Tab>
                </Tabs>

                <section className="projects-section">
                    {this.getContent()}
                </section>
            </div>
        );
    }
}

export default Projects;