import React from 'react';
import LandingPage from './LandingPage';
import About from './About';
import Projects from './projects/Projects';
import Resume from './Resume';
import Contact from './Contact';
import {Switch, Route} from 'react-router-dom';

const Main = (props) => {
    console.log("MAIN" + props.styling);
    return ( 
            <Switch> 
                <Route exact path="/">
                    <LandingPage styling={props.styling}/>
                </Route>
                <Route path="/about">
                    <About styling={props.styling}/>
                </Route>
                <Route path="/projects">
                    <Projects styling={props.styling}/>
                </Route>
                <Route path="/resume">
                    <Resume styling={props.styling}/>
                </Route>
                <Route path="/contact">
                    <Contact styling={props.styling}/>
                </Route>
            </Switch>
        
    );
}

export default Main;