import React, {Component} from 'react';
import {Grid, Cell, Card, CardTitle, CardActions, CardText, Button, CardMenu, IconButton} from 'react-mdl';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
const Resume = (props) => {
    return (
        <div style={{...props.styling.headerColor, height: '100%'}}>
            <Grid style={{width: '100%', display: 'flex', justifyContent: 'center'}}>
                <Cell col={4}>
                    <Card shadow={0} style={{width: '100%', margin: 'auto'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://coolkidproblems.com/wp-content/uploads/2017/06/coming-soon-1.jpg) center / cover'}}></CardTitle>
                        <CardText>
                            This page is still under development! Please check back soon!
                        </CardText>
                        <CardActions border style={{display: 'flex', justifyContent: 'center'}}>
                            <FontAwesomeIcon icon={['fas', 'drafting-compass']}></FontAwesomeIcon>
                        </CardActions>
                    </Card>
                </Cell>
            </Grid>
        </div>
    );
}
export default Resume;
